@import url('https://fonts.googleapis.com/css2?family=Kanit:wght@300&display=swap');

*{
    box-sizing: border-box;
    margin: 0;
}
body{
    font-family: 'Kanit', sans-serif;
    width: 100vw;
    height: 100vh;
}
.main-container{
    width: 100vw;
    height: 100vh;
    padding-top: 100px;
    background-color: #f5f7fb;
    display: flex;
    align-items: center;
    justify-content: center;
}

.blob{
    position: absolute;
    top: 0;
    left: 0;
    width: 50vmax;
    transform-origin: 50% 50%;
    animation: move 10s ease-in-out infinite;
}

@keyframes move{
    0%{transform: scale(1) translate(-60px, -50px)}
    38%{transform: scale(0.8) translate(30vw, 30vh) rotate(160deg)}
    40%{transform: scale(0.8) translate(30vw, 30vh) rotate(160deg)}
    78%{transform: scale(1.5) translate(-20vw, 50vh) rotate(-20deg)}
    80%{transform: scale(1.5) translate(-20vw, 50vh) rotate(-20deg)}
    100%{transform: scale(1) translate(-60px, -50px)}
}
.content-container{
    z-index: 10;
    max-width: 950px;
}

.menu{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    gap: .6rem;
}
.page-title{
    color: #293264;
    font-size: 2.5rem;
}
.page-description{
    color: rgba(41, 50, 100, 1);
}
.start-button{
    color: #fff;
    border: none;
    padding: 16px 32px;
    background-color: #4d5b9e;
    border-radius: 5px;
    font-family: inherit;
    font-weight: 500;
    font-size: 16px;
    line-height: 19px;
}

.start-content-container{
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: center;
}
.end-div{
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    gap: 1rem;
    margin-top: .2rem;
}
.content-container{
    padding: 0 16px;
}
.question-container{
    margin-bottom: .65rem;
    width: 100%;
    text-align: left;
}
.question-title{
    font-size: .5rem;
    color: rgb(41, 50, 100);
    margin-bottom: .3rem;
}
.answer{
    background-color: transparent;
    border: 1px solid #4d5b9e;
    font-size: .6rem;
    border-radius: .3rem;
    margin-right: .6rem;
    color: rgb(41, 50, 100);
    padding: 2px 8px;
}
.line{
    margin-top: .5rem;
    width: 100%;
    border-top: .8px solid #c4c8e0;
}
.selected{
    background-color: #d6dbf5;
}
.check{
    color: #fff;
    background-color: #4d5b9e;
    border: none;
    border-radius: 10px;
    padding: 14px 24px;
}
.score{
    font-weight: 700;
    font-size: 1rem;
}
#correct.answer{
    background-color: #94d7a2;
    border: 1px solid #94d7a2;
}
#incorrect.answer{
    background-color: #f88cbc;
    border: 1px solid #f88cbc;
}
#non-selected.answer{
    color: rgba(41, 50, 100, .4);
    border: 1px solid rgba(41, 50, 100, .4);
}
@media only screen and (min-width:350px){
    .question-title{
        font-size: .85rem;
    }
    .answer{
        font-size: .65rem;
        margin-bottom: .3rem;
    }
}
@media only screen and (min-width:400px){
    .question-title{
        font-size: 1.1rem;
    }
    .answer{
        font-size: .8rem;
        margin-bottom: .4rem;
    }
    .check{
        font-size: 1rem;
    }
    @keyframes move{
        0%{transform: scale(1) translate(-60px, -50px)}
        38%{transform: scale(0.8) translate(70vw, 30vh) rotate(160deg)}
        40%{transform: scale(0.8) translate(70vw, 30vh) rotate(160deg)}
        78%{transform: scale(1.5) translate(-10vw, 50vh) rotate(-20deg)}
        80%{transform: scale(1.5) translate(-10vw, 50vh) rotate(-20deg)}
        100%{transform: scale(1) translate(-50px, -50px)}
    }
}
@media only screen and (min-width:630px){
    .content-container{
        padding: 0 32px;
    }
    .question-container{
        margin-bottom: .35rem;
    }
    .question-title{
        font-size: 1.1rem;
        margin-bottom: .9rem;
    }
    .answer{
        font-size: .9rem;
        margin-bottom: .4rem;
    }
    .check{
        font-size: 1.2rem;
    }
    @keyframes move{
        0%{transform: scale(1) translate(100px, -100px)}
        38%{transform: scale(0.8) translate(90vw, 30vh) rotate(160deg)}
        40%{transform: scale(0.8) translate(90vw, 30vh) rotate(160deg)}
        78%{transform: scale(1.5) translate(0vw, 50vh) rotate(-20deg)}
        80%{transform: scale(1.5) translate(0vw, 50vh) rotate(-20deg)}
        100%{transform: scale(1) translate(100px, -100px)}
    }
}
@media only screen and (min-width: 1000px){
    .question-container{
        margin-bottom: 1.2rem;
    }
    .question-title{
        font-size: 1.4rem;
        margin-bottom: 1.2rem;
    }
    .answer{
        font-size: .9rem;
        margin-bottom: .5rem;
    }
    .check{
        font-size: 1.3rem;
    }
    @keyframes move{
        0%{transform: scale(1) translate(100px, -100px)}
        38%{transform: scale(0.8) translate(85vw, 30vh) rotate(160deg)}
        40%{transform: scale(0.8) translate(85vw, 30vh) rotate(160deg)}
        78%{transform: scale(1.5) translate(0vw, 50vh) rotate(-20deg)}
        80%{transform: scale(1.5) translate(0vw, 50vh) rotate(-20deg)}
        100%{transform: scale(1) translate(100px, -100px)}
    }
}
